// MOBILE L: FROM 576 TO 768
@media screen and (min-width: $mobile-l) and (max-width: $to-tablet) {
	.graphics {
		&.portfolio {
			main .columns .column {
				.categories p {
					font-size: rem(16px); } } } } }

// TABLET: FROM 768 TO 1023
@media screen and (max-width: $to-desktop-s) {
	.audio {
		header {
			.navbar-brand-txt {
				display: none; }
			.navbar .navbar-menu {
				background-color: $grey;
				-webkit-box-shadow: 0 0 0 $transparent;
				box-shadow: 0 0 0 $transparent;
				.navbar-end {
					text-align: center; }
				ul {
					flex-direction: column;
					border-top: rem(1px) solid transparentize($white, .8);
					padding-top: rem(40px);
					padding-bottom: rem(22px);
					// margin-top: rem(10px)
					font-size: rem(20px);
					li {
						margin-right: 0;
						margin-top: rem(20px);
						&:hover {
							border-bottom: 0 solid $transparent;
							padding-bottom: 0; }
						&:first-child {
							margin-top: 0; } } } } } }

	.graphics {
		header .navbar .navbar-menu {
			.navbar-end {
				position: fixed;
				width: 50%;
				height: 100%;
				top: 0;
				right: -50%;
				text-align: center;
				transition: all .5s;
				button {
					display: block; }
				.background {
					position: fixed;
					left: 0;
					top: 0;
					z-index: -1;
					width: 100vw;
					height: 100vh;
					background-color: transparentize($black, .25);
					opacity: 0;
					transition: all .5s; }
				.closeMenu {
					position: absolute;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column;
					z-index: 5;
					height: 20px;
					width: 20px;
					top: 20px;
					left: 30px;
					// @include animation (closeButtonIn, .5s, 1, ease)
					// @include animation-fill-mode (forwards)
					&:hover {
						cursor: pointer;
						span:nth-child(1) {
							transform: rotate(135deg); }
						span:nth-child(2) {
							transform: rotate(45deg); } }
					span {
						position: absolute;
						height: 3px;
						width: 100%;
						background-color: $black;
						transition: all .2s;
						&:nth-child(1) {
							transform: rotate(45deg); }
						&:nth-child(2) {
							transform: rotate(-45deg); } } }
				&.mobileMenuOpen {
					right: 0;
					.closeMenu {
						@include animation (closeButtonIn, 2s, 1, ease);
						@include animation-fill-mode (forwards); }
					.background {
						opacity: 1; } } }
			ul {
				position: fixed;
				top: 0;
				background-color: $white;
				height: 100%;
				width: inherit;
				margin: 0;
				flex-direction: column;
				justify-content: flex-end;
				align-items: flex-start;
				padding: rem(20px) rem(60px);
				font-size: rem(28px);
				box-shadow: rem(-4px) 0 rem(50px) transparentize($black, .75);
				transition: all .5s; }
			li {
				font-weight: normal;
				color: transparentize($black, .5);
				margin: 0;
				margin-bottom: rem(20px);
				&:last-child {
					margin-bottom: 0; }
				.current-page {
					color: $black;
					font-weight: 600;
					border-bottom: 0;
					&:after {
						content: '';
						position: absolute;
						left: rem(-40px);
						top: 50%;
						transform: translateY(-50%);
						background-color: $black;
						height: rem(2px);
						width: rem(20px); } }
				&:hover {
					color: $black; } } }

		&.about {
			main {
				h1 ~ .columns {
					display: flex;
					flex-direction: column-reverse; }
				.card {
					margin-bottom: 50px; }
				.columns .column {
					display: flex;
					flex-direction: column;
					align-items: center;
					flex-basis: auto;
					p {
						text-align: justify;
						text-align-last: center; } } } }
		&.contact {
			main {
				form {
					& * {
						width: 80%; } } }
			// .waves
			// 	.wave1
			// 		background-image: url("../src/graphics/contact/contact-tablet-wave1.svg")
			// 	.wave2
 }			// 		background-image: url("../src/graphics/contact/contact-tablet-wave2.svg")
		&.portfolio {
			main {
				.columns .column .categories {
					height: calc(100vw / 100 * 80 / 2 - 10px); } }
			.modal {
				.presentation .content {
					display: none; } } } } }

@media screen and (min-width: $tablet) {
	.no-desktop {
		display: none !important; }

	.audio {
		&.portfolio {
			main {
				.columns .column {
					.categories {
						height: calc(((100vw - 1.875rem) / 100 * 80) / 2 - 0.625rem); } } } } }
	.graphics {
		&.home {
			.action {
				h1 {
					font-size: rem(80px); } } }
		&.about {
			main {
				.columns .column {
					&:nth-child(2) .card {
						width: 80%; } }
				.action {
					a {
						width: auto; } } } }

		&.portfolio {
			main {
				.filterButtons p:nth-child(2) {
					display: none; } } } } }

@media screen and (min-width: $tablet) and (max-width: $to-desktop-s) {
	.graphics {
		&.home {
			.services {
				div:nth-child(2) .container .columns {
					flex-wrap: wrap;
					.column {
						flex: 0 50%;
						p {
							text-align: center; } } } } } } }

// DESKTOP: FROM 1024 TO 1215
@media screen and (min-width: $desktop-s) {
	.audio {
		&.home {
			main {
				.home-icons svg {
					isolation: isolate;
					width: 100%;
					height: rem(50px);
					padding: 0 rem(20px);
					margin-bottom: rem(20px);
					fill: $black;
					@include transition (fill, .2s, ease); }
				.home-mastering {
					margin-left: 0; } } } }
	.graphics {
		header {
			&.header-hover {
				transition: background-color .2s;
				&:hover {
					background-color: transparentize($black, .5) !important; } }
			.navbar .navbar-burger {
				display: none; } }
		&.home {
			.showcase {
				.columns {
					h2.column.selected:after, h2:hover:after {
						width: 95%; } }
				.work-samples {
					.back-sample-1, .back-sample-2 {
						display: block; } } }
			.action {
				h1 {
					font-size: rem(96px);
					line-height: rem(115px); } } }
		&.portfolio {
			main {
				.columns .column {
					.categories {
						height: calc(100vw / 100 * 80 / 3 - 10px); } } } }
