	.graphics {
		&.home {
			.showcase .columns h2 {
				font-size: rem(28px); } }
		&.portfolio {
			main {
				.columns .column {
					.categories {
						height: calc(100vw / 100 * 80 / 4 - 10px); } } } } } }

