			// 	div
			// 		background-size: contain
			// 	.wave1
			// 		background-image: url("../src/graphics/contact/contact-mobile-wave1.svg")
			// 	.wave2
 } } }			// 		background-image: url("../src/graphics/contact/contact-mobile-wave2.svg")

// MOBILE-L: UP TO 425
@media screen and (max-width: $mobile-l) {
	.graphics {
		main.container {
			max-width: 100%;
			padding: 0 20px; }
		&.home {
			.showcase {
				.columns h2.column.selected:after {
					width: 60%; } }
			.action {
				div {
					a {
						padding: 15px 50px; } } } }
		&.about {
			main .action a {
				width: 100%;
				padding: 15px 0; } }
		&.portfolio {
			main {
				.columns .column .categories {
					height: calc(100vw - 50px);
					p {
						font-size: rem(16px); } } } } } }

// MOBILE-M: UP TO 375
@media screen and (max-width: $mobile-m) {
	.graphics {
		&.home {
			.showcase {
				.columns h2.column.selected:after {
					width: 70%; } } }
		&.about {
			main {
				.action a {
					width: 100%; } } } } }

// MOBILE-S: UP TO 320
@media screen and (max-width: $mobile-s) {
	.graphics {
		&.home {
			.showcase {
				.columns {
					h2.column.selected:after, h2:hover:after {
						width: 80%; } } } } } }
