.graphics.home {
	background: linear-gradient(to bottom, $purple 0%, $celeste 40.1%, $blue 100%);

	.hero {
		display: flex;
		background-image: url(../src/graphics/home/Smoke_compressed.png);
		background-size: cover;
		filter: contrast(2);
		position: relative;
		justify-content: center;
		color: $white;
		position: relative;
		height: 150vh;
		min-height: rem(360px);
		&>div {
			margin-top: calc(-50vh + 5rem);
			#typewriter {
				text-align: center;
				position: relative;
				margin-bottom: rem(100px);
				font-size: rem(48px);
				letter-spacing: rem(4.8px);
				text-transform: uppercase;
				text-shadow: 0 rem(4px) rem(4px) transparentize($black, .75);
				line-height: 1.75;
				h1 {
					font-size: rem(48px);
					margin-bottom: 0;
					&:nth-child(2) {
						position: relative;
						&:empty {
							height: rem(84px); }
						&:after {
							content: '';
							background-color: $white;
							height: rem(60px);
							width: rem(2px);
							position: absolute;
							top: 50%;
							transform: translateY(-50%);
							will-change: auto;
							animation: caret .7s infinite; } } } }
			svg {
				position: absolute;
				left: 50%;
				transform: translateX(-50%); } } }
	.services {
		filter: drop-shadow(0 0 50px transparentize($black, .5));
		margin-top: -50vh;
		.top-wave {
			display: block;
			width: 100%;
			transform: translateY(1px); }
		.bottom-wave {
			display: block;
			width: 100%;
			transform: translateY(-1px); }
		div:nth-child(2) {
			background-color: $white;
			.container {
				padding: rem(50px) 0 rem(100px) 0;
				h3 {
					text-align: center;
					font-size: rem(40px);
					color: $black;
					margin-bottom: rem(50px); }
				.column {
					display: flex;
					text-align: center;
					flex-direction: column;
					align-items: center;
					padding: rem(20px);
					h4 {
						color: $black;
						font-weight: 700;
						margin-bottom: 10px; }
					p {
						color: $darkBlue;
						text-align: left; } } } } }
	.showcase {
		margin: 200px 0;
		text-align: center;
		color: $white;
		.work-samples {
			display: flex;
			position: relative;
			justify-content: center;
			& * {
				background-color: $white;
				border: rem(5px) solid #EBEBEB;
				border-radius: rem(20px); }
			.main-sample {
				width: rem(460px);
				height: rem(260px);
				z-index: 20;
				box-shadow: 0 0 rem(50px) transparentize($black, .7), 0 0 rem(20px) transparentize($black, .85), 0 0 rem(10px) transparentize($black, .9); }
			.back-sample-1 {
				display: none;
				height: 55%;
				width: rem(250px);
				position: absolute;
				left: calc(50% - 28.75rem / 2 - 15.625rem / 2);
				top: calc(-55% / 2);
				box-shadow: 0 0 rem(25px) transparentize($black, .75); }
			.back-sample-2 {
				display: none;
				height: 60%;
				width: rem(275px);
				position: absolute;
				right: calc(50% - 28.75rem / 2 - 17.1875rem / 1.5);
				bottom: calc(-30% / 2);
				box-shadow: 0 0 rem(25px) transparentize($black, .75); } }

		.particles {
			div {
				position: absolute;
				&::after {
					content: '';
					position: absolute;
					background-color: $white;
					box-shadow: 0 0 rem(10px) $white;
					border-radius: 100%; }
				&:nth-child(1) {
					top: -10%;
					right: 0;
					animation: particle1-position 5s ease infinite;
					&::after {
						height: rem(15px);
						width: rem(15px);
						animation: particle1-scale 5s ease infinite; } }
				&:nth-child(2) {
					top: -28%;
					left: 25%;
					z-index: 10;
					animation: particle2-position 5s ease infinite;
					animation-delay: 1s;
					&::after {
						height: rem(8px);
						width: rem(8px);
						animation: particle2-scale 5s ease infinite;
						animation-delay: 1s; } }
				&:nth-child(3) {
					height: 100px;
					top: 30%;
					left: 10%;
					animation: particle3-position 5s ease infinite;
					animation-delay: .5s;
					&::after {
						height: rem(12px);
						width: rem(12px);
						animation: particle3-scale 5s ease infinite;
						animation-delay: .5s; } }
				&:nth-child(4) {
					top: 66%;
					left: 60%;
					animation: particle4-position 3s ease infinite;
					&::after {
						height: rem(6px);
						width: rem(6px);
						animation: particle4-scale 3s ease infinite; } } } }

		.description {
			display: flex;
			margin: rem(90px) 0 rem(50px) 0;
			position: relative;
			filter: drop-shadow(0px rem(2px) rem(4px) transparentize($black, .75));
			justify-content: center;
			p {
				padding: rem(5px);
				font-size: rem(24px);
				width: fit-content;
				border-style: solid;
				border-width: rem(10px) rem(10px) rem(10px) rem(10px);
				-moz-border-image: url(../src/graphics/home/borders.svg) 10 10 10 10 round repeat;
				-webkit-border-image: url(../src/graphics/home/borders.svg) 10 10 10 10 round repeat;
				-o-border-image: url(../src/graphics/home/borders.svg) 10 10 10 10 round repeat;
				border-image: url(../src/graphics/home/borders.svg) 10 10 10 10 round repeat; } }
		.columns {
			text-transform: uppercase;
			h2 {
				font-size: rem(24px);
				font-weight: 700;
				text-shadow: 0 rem(3px) rem(4px) transparentize($black, .75);
				position: relative;
				&:hover {
					cursor: pointer;
					text-shadow: 0 rem(4px) rem(7px) transparentize($black, .25);
					&:after {
						content: '';
						background-color: transparentize($grey, .5);
						height: rem(16px);
						width: 80%;
						position: absolute;
						left: 50%;
						transform: translateX(-50%) translateY(50%);
						top: 25%;
						z-index: -1; } }
				&.column {
					&.selected {
						text-shadow: 0 rem(4px) rem(7px) transparentize($black, .25);
						&:after {
							content: '';
							background-color: $grey;
							height: rem(16px);
							width: 80%;
							position: absolute;
							left: 50%;
							transform: translateX(-50%) translateY(50%);
							top: 25%;
							z-index: -1; } } } } } }
	.action {
		h1 {
			margin: 0;
			padding: 0 10px;
			color: white;
			font-size: rem(96px);
			font-weight: 900;
			line-height: rem(115px);
			position: relative;
			z-index: 5;
			span {
				display: block;
				&:nth-child(1) {
					text-shadow: 0 rem(4px) rem(4px) transparentize($black, .75); }

				&:nth-child(2) {
					color: $homeBlue;
					text-shadow: 0 rem(4px) rem(4px) transparentize($black, .85); } } }
		img {
			display: block;
			width: 100%;
			margin-top: rem(-320px);
			transform: translateY(1px); }
		div {
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $white;
			height: 350px;
			a {
				// background-color: $homeBlue
				background-color: #187df7;
				color: $white;
				// text-shadow: 0 1px 2px transparentize($black, .8)
				border: 0;
				border-radius: rem(5px);
				font-size: rem(18px);
				font-weight: 700;
				padding: 15px 80px;
				box-shadow: 0 2px 2px transparentize($homeBlue, .8),0 5px 10px transparentize($homeBlue, .3);
				transition: all .2s;
				&:hover {
					background-color: #1063c7;
					box-shadow: 0 2px 2px transparentize(#1063c7, .8),0 5px 10px transparentize(#1063c7, .3); } } } }
	footer {
		color: $black;
		background-color: $white;
		.footer-top .footer-icons {
			a {
				background-color: $homeBlue;
				box-shadow: 0 0 0; }
			i {
				color: $white; } }
		.footer-divider {
			background-color: transparentize($grey, .8); }
		.footer-bottom a p {
			color: $darkBlue;
			&:hover {
				color: $black; } } } }
