.graphics.services {
	background: linear-gradient(to bottom, $purple 0%, $celeste 40.1%, $purple 100%);

	main {
		position: relative;
		z-index: 10;

		.columns {
			margin-bottom: rem(100px);
			text-align: left;
			text-shadow: 1px 1px 2px transparentize($black, .85);

			h2 {
				margin-bottom: rem(20px);
				border-bottom: rem(1px) solid;
				padding-bottom: rem(5px);
				width: fit-content;
				text-transform: uppercase;
				font-size: rem(20px);
				font-weight: 700;
				text-align: left; }

			.column {
				&:nth-child(1) {
					display: flex;
					flex-direction: column;
					justify-content: center; }
				&:nth-child(2) {
					div {
						background-color: $white;
						border-radius: rem(10px);
						height: rem(277px);
						width: 100%;
						box-shadow: 0 0 rem(50px) transparentize($black, .9), 0 0 rem(15px) transparentize($black, .8), 0 0 rem(5px)transparentize($black, .9); } } }

			&:nth-child(even) {
				.column {
					&:nth-child(1) {
						padding-right: rem(10px); }
					&:nth-child(2) {
						padding-left: rem(10px); } } }

			&:nth-child(odd) {
				flex-direction: row-reverse;
				// text-align: right
				.column {
					// display: flex
					// flex-direction: column
					// align-items: flex-end
					&:nth-child(1) {
						padding-left: rem(20px); }
					&:nth-child(2) {
						padding-right: rem(20px); } }
				h2 {
					text-align: right; } }

			&:last-child {
				margin-bottom: rem(200px); } } }

	.waves {
		div {
			z-index: 0;
			&.wave1 {
				top: -50%;
				background-image: url("../src/graphics/services/services-wave1.svg"); }
			&.wave2 {
				bottom: 0;
				background-image: url("../src/graphics/services/services-wave2.svg"); }
			&.wave3 {
				bottom: 0;
				background-image: url("../src/graphics/services/services-wave3.svg"); } } } }
